* {
    font-family: 'Baloo Chettan', cursive;
}
body {
    min-height: 100%;
    background-color: gray;
    background-image: url(/react-game/static/media/back.39738688.jpg);
}

.box-shadow {
    box-shadow: 0 3px 5px 0 rgba(0,0,0,0.08);
    transition: box-shadow 0.3s ease-in-out;
}

.box-shadow:hover {
    box-shadow: 0 5px 15px 2px rgba(0, 0, 0, 0.9);
}
